@import '../../../common/styles/sass/variables';

.nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 300px;

    @media screen and (max-width: 400px) {
        display: none;
    }

    a {
        letter-spacing: .1px;
        color: $textColor;
        font-size: 15px;
        padding: 0;
        text-decoration: none;
        background-color: transparent;
        font-weight: 400;
        line-height: 1.95;
        cursor: pointer;
        transition: .3s;

        border-bottom: 4px solid $mainBackgroundColor;

        @media screen and (max-width: 400px) {
            background-color: transparent;
            border-bottom: 4px solid transparent;
        }

        &:hover {
            border-bottom: 4px solid $secondColor;
            color: $secondColor;
        }
    }
}