@import '../../common/styles/sass/variables';
@import '../../common/styles/sass/mixins';

.contactBlock {
    text-align: center;
    padding: 50px 0;
    min-height: 40vh;

    .contactContainer {
        @include container();
        flex-direction: column;

        .form {
            display: flex;
            flex-direction: column;

            .input {
                font-family: $mainFontFamily;
                padding: 15px 5px;
                margin-bottom: 10px;
                min-width: 500px;
                background-color: $secondBackgroundColor;
                color: $textColor;
                border: 2px solid $secondBackgroundColor;
                outline: none;

                &:focus {
                    border-bottom: 2px solid $secondColor;
                }

                @media screen and (max-width: 658px) {
                    min-width: 280px;
                }
            }

            .textarea {
                font-family: $mainFontFamily;
                resize: none;
                padding: 5px 5px;
                margin-bottom: 20px;
                height: 200px;
                background-color: $secondBackgroundColor;
                color: $textColor;
                outline: none;
                border: 2px solid $secondBackgroundColor;

                &:focus {
                    border-bottom: 2px solid $secondColor;
                }

                @media screen and (max-width: 658px) {
                    height: 120px;
                }
            }

            .btnInput {
                background-color: $secondColor;
                font-size: 14px;
                border: 1px solid $secondColor;
                padding: 10px 35px;
                border-radius: 30px;
                color: $textColor;
                font-weight: 400;
                line-height: 1.5;
                cursor: pointer;
                position: relative;
                align-self: center;
                width: fit-content;
            }
        }
    }
}


